import * as React from "react";
import { Carousel } from "zarm";
import { createCss } from "../../utils/css";
import "./index.css";
import "zarm/dist/zarm.min.css"; // 加载css
interface Props {
  onChange: (index: number) => any;
}

const css = createCss("dd-ui-layout-swiper");

const LayoutSwiper: React.FC<Props> = props => {
  const onChange =
    typeof props.onChange === "function" ? props.onChange : () => {};

  return (
    <Carousel prefixCls={css("wrapper")} onChange={onChange} direction="left">
      {props.children as any}
    </Carousel>
  );
};

export default LayoutSwiper;
